import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { useLocation } from "@reach/router"
import { initializeAndTrack } from 'gatsby-plugin-gdpr-cookies'
import CookieConsent, { Cookies } from "react-cookie-consent";
import ScrollTop from "./scroll-top"

export default function Layout({ children }) {
  const location = useLocation()
  return (
    <>
      <CookieConsent
        location="bottom"
        buttonText="Cookie使用を許可"
        style={{ background: "#2B373B", fontSize: "12px" }}
        buttonStyle={{ color: "#4e503b", fontSize: "13px" }}
        expires={150}
        onAccept={() => {
          initializeAndTrack(location)
          Cookies.set('gatsby-gdpr-google-analytics', true)
          Cookies.set('gatsby-gdpr-google-tagmanager', true)
        }}
      >
        当サイトでは、お客様のアクセス傾向を分析してより良いサービスを提供するため、Cookieを使用いたします。{" "}
        <span style={{ fontSize: "10px" }}>
          <Link to={`/about-website`}>当サイトについて</Link>
        </span>
      </CookieConsent>

      <header>
        <h3>ここはheaderです</h3>
        <StaticImage
          src="../images/hotsoul_logo_rct_trans_L.png"
          alt="Hotsoul Logo"
          placeholder="blurred"
          width={600}
          height={200}
        />
        <nav>
          <ul>
            <li>
              <Link to={`/`}>HOME</Link>
            </li>
            <li>
              <Link to={`/case-study`}>お悩み解決事例</Link>
            </li>
            <li>
              <Link to={`/dx-council`}>やさしいDX推進協議会</Link>
            </li>
            <li>
              <Link to={`/column`}>コラム</Link>
            </li>
            <li>
              <Link to={`/about-us`}>会社案内</Link>
            </li>
            <li>
              <Link to={`/contact-us`}>お問合せ</Link>
            </li>
          </ul>
        </nav>
      </header>
      <main>{children}</main>
      <footer>
        <h3>ここはfooterです</h3>
        © {new Date().getFullYear()} HotSoul Inc.
      </footer>
      <ScrollTop/>
    </>     
  )
}
